import { Injectable } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

// TODO: add page size to configuration ?!?
const DefaultPageSize = 20;

@Injectable({
  providedIn: 'root'
})
export class PaginatorService {
  constructor(private router: Router, private route: ActivatedRoute) {
  }

  public getPageSize(): number {
    const queryParams = this.route.snapshot.queryParams;
    return queryParams.page_size || DefaultPageSize;
  }

  public setPageSize(pageSize: number = DefaultPageSize) {
    const queryParams = {page_size: undefined};
    Object.assign(queryParams, this.route.snapshot.queryParams);
    queryParams.page_size = pageSize;
    this.router.navigate([], {
      relativeTo: this.route,
      queryParams
    }).catch(() => {
      // TODO: handle navigation error
    });
  }

  public increasePageSize() {
    this.setPageSize(this.getPageSize() + DefaultPageSize);
  }
}
