import { Component, Input, OnInit } from '@angular/core';

export enum LineDirection {
  Horizontal,
  Vertical,
}

export enum LineColor {
  None,
  Green,
  Gray,
  Orange,
  Yellow,
  Red,
}

export enum LineType {
  None,
  Solid,
  Progress
}

@Component({
  selector: 'app-status-line',
  templateUrl: './status-line.component.html',
  styleUrls: ['./status-line.component.scss']
})
export class StatusLineComponent implements OnInit {
  @Input() color: LineColor = LineColor.None;
  @Input() direction: LineDirection = LineDirection.Horizontal;
  @Input() type: LineType = LineType.None;
  classes: {[className: string]: boolean} = {};

  private readonly DirectionClasses: { [direction in LineDirection]: string } = {
    [LineDirection.Horizontal]: 'status-line-horizontal',
    [LineDirection.Vertical]: 'status-line-vertical',
  };

  private readonly LineType = LineType;

  private readonly ColorClasses: { [color in LineColor]: string } = {
    [LineColor.None]: 'status-line-color-none',
    [LineColor.Green]: 'status-line-color-green',
    [LineColor.Gray]: 'status-line-color-gray',
    [LineColor.Orange]: 'status-line-color-orange',
    [LineColor.Yellow]: 'status-line-color-yellow',
    [LineColor.Red]: 'status-line-color-red',
  };

  constructor() {
  }

  ngOnInit() {
    this.classes[this.ColorClasses[this.color]] = true;
    this.classes[this.DirectionClasses[this.direction]] = true;
  }

}
