import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IFilterOptionConfig } from '../../../ui-api/interfaces/route-config/filter-option-config';
import { FilterTypes } from '../../../ui-api/interfaces/route-config/filter-types';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-objects-list-filtering',
  templateUrl: './objects-list-filtering.component.html',
  styleUrls: ['./objects-list-filtering.component.scss']
})
export class ObjectsListFilteringComponent implements OnInit, OnDestroy {
  showDetail = false;
  filterOptions: IFilterOptionConfig[];
  activeFilterOption: IFilterOptionConfig;
  private readonly FilterTypes = FilterTypes;
  private routeDataSubscription: Subscription = null;

  constructor(private activatedRoute: ActivatedRoute) {
  }

  showDetailSwitch(event: Event, filterOption?: IFilterOptionConfig) {
    event.stopPropagation();
    this.showDetail = !this.showDetail;
    this.activeFilterOption = filterOption;
  }

  ngOnInit() {
    this.routeDataSubscription = this.activatedRoute.data.subscribe((data) => {
      if (data.config && data.config.filterOptions) {
        this.filterOptions = data.config.filterOptions;
      } else {
        this.filterOptions = null;
      }
    });
  }

  ngOnDestroy(): void {
    if (this.routeDataSubscription) {
      this.routeDataSubscription.unsubscribe();
      this.routeDataSubscription = null;
    }
  }

}
