import { Pipe, PipeTransform } from '@angular/core';
import { StatusValue } from '../interfaces/object-status';
import { LineColor } from './status-line.component';

@Pipe({
  name: 'lineColor'
})
export class LineColorPipe implements PipeTransform {
  transform(value: StatusValue): LineColor {
    switch (value) {
      case StatusValue.Enabled:
        return LineColor.Green;
      case StatusValue.Disabled:
        return LineColor.Gray;
      case StatusValue.Waiting:
        return LineColor.Orange;
      case StatusValue.Warning:
        return LineColor.Yellow;
      case StatusValue.Error:
        return LineColor.Red;
      default:
        return LineColor.None;
    }
  }

}
