import { from, interval, Subscription, timer } from 'rxjs';
import { delayWhen, finalize } from 'rxjs/operators';

export const DEFAULT_BOOST_INTERVALS = [100, 200, 400, 700, 1000];

export class RefreshTimer {
  private readonly callback: () => void;
  private readonly pauseOnIdle: boolean = false;

  private timerInterval: number;
  private timerSubscription: Subscription;
  private executing = false;
  private lastActivity: number = Date.now();

  constructor(timerInterval: number, callback: () => void, autoStart: boolean = true, pauseOnIdle: boolean = true) {
    this.callback = callback;
    this.timerInterval = timerInterval;

    if (autoStart) {
      this.start();
    }

    if (pauseOnIdle) {
      this.pauseOnIdle = true;
      window.addEventListener('mousemove', () => {
        this.setLastActivity(Date.now());
      });
      window.addEventListener('keydown', () => {
        this.setLastActivity(Date.now());
      });
    }
  }

  setLastActivity(lastActivity: number) {
    const refreshNow = this.isStarted && this.isIdle();
    this.lastActivity = lastActivity;

    if (refreshNow) {
      this.executeCallback();
    }
  }

  isIdle(): boolean {
    if (this.pauseOnIdle) {
      const currentDate = Date.now();
      const secondsSinceLastActivity = (currentDate - this.lastActivity) / 1000;
      return secondsSinceLastActivity > 30;
    } else {
      return false;
    }
  }

  private executeCallback() {
    if (!this.executing && !this.isIdle()) {
      this.executing = true;
      this.callback();
      this.executing = false;
    }
  }

  get isStarted() {
    return !!this.timerSubscription;
  }

  setInterval(timerInterval: number, start: boolean = false) {
    const wasStarted = this.isStarted;
    this.stop();
    this.timerInterval = timerInterval;
    if (wasStarted || start) {
      this.start();
    }
  }

  start() {
    this.stop();
    this.lastActivity = Date.now();
    this.timerSubscription = timer(
      this.timerInterval, this.timerInterval
    ).subscribe(() => {
      this.executeCallback();
    });
  }

  stop() {
    if (this.isStarted) {
      this.timerSubscription.unsubscribe();
      this.timerSubscription = null;
    }
  }

  boost(boostIntervals: number[] = DEFAULT_BOOST_INTERVALS) {
    const wasStarted = this.isStarted;
    this.stop();

    this.timerSubscription = from(boostIntervals).pipe(
      delayWhen(i => {
        return interval(i);
      }),
      finalize(() => {
        this.stop();
        if (wasStarted) {
          this.start();
        }
      })
    ).subscribe(
      () => {
        this.executeCallback();
      },
    );
  }
}
