import { Component, Input, OnInit } from '@angular/core';
import { IAction } from '../interfaces/actions/action';
import { NotificationService } from '../../../ui-api/notification.service';
import { RefreshService } from '../../../ui-api/refresh.service';

@Component({
  selector: 'app-object-actions',
  templateUrl: './object-actions.component.html',
  styleUrls: ['./object-actions.component.scss']
})
export class ObjectActionsComponent implements OnInit {
  @Input() objectActions: IAction[];
  @Input() maxVisibleActions: number;
  @Input() listStateActions = false;

  private visibleAction: IAction[] = null;
  private inMenuActions: IAction[] = null;

  constructor(private notificationService: NotificationService, private refreshService: RefreshService) { }

  ngOnInit() {
    if (this.objectActions) {
      if (!this.listStateActions) {
        // we are not interested in visible actions on states
        this.visibleAction = this.objectActions.slice(0, this.maxVisibleActions);
        this.inMenuActions = this.objectActions.slice(this.maxVisibleActions);
      }
    }
  }
}
